	module.exports = {"data":{"wordpressPost":{"title":"This Mesmerising Winged Liner Is All Instagram Wants To Talk About","content":"<p>My eyeliner skills are not exactly what you&#8217;d call ace.</p>\n<p>I don&#8217;t have the steadiest of hands and well, let&#8217;s just say the results tend to be wonky at best and disastrous at worst. Perhaps because of that I&#8217;m always super impressed when I spy women (or men) who can apply liner with finesse. <a href=\"https://www.instagram.com/p/BUbV9F_jJc8/?taken-by=nicoleetait&amp;hl=en\"><strong>Makeup artist Nicole Tait</strong></a> is certainly able to do that &#8211; and then some.</p>\n<p>In fact, the <strong>Brisbane-based makeup artist</strong> has eyeliner talent like you&#8217;ve never seen before and the whole of Instagram is buzzing about it. Makeup fans there are fascinated by Nicole&#8217;s incredible layering technique which works to create a 3-D effect of sorts.</p>\n<p>The more you look at it the more mesmerising it becomes &#8211; if that&#8217;s possible. Nicole has used five different coloured liners to create the effect which sees &#8216;wings&#8217; running from the inner eye to the outer corner and then under the eye.</p>\n<p>The very top line has an added effect with <strong>four tiny dots</strong>. The bottom line does too. And right in the middle there is Nicole&#8217;s amazing, crystal-clear blue eyes. What to say except HOW does she do it? In an interview with <a href=\"http://www.allure.com/story/intricate-winged-eyeliner-instagram?mbid=nl_trend_blast&amp;CNDID=44795737&amp;spMailingID=11112309&amp;spUserID=MTUzNDk3Nzc4OTQ0S0&amp;spJobID=1162212330&amp;spReportId=MTE2MjIxMjMzMAS2\">Allure</a>, Nicole said said she simply started with one line and kept adding another and another.</p>\n<p>Nicole stopped only when she ran out of eyelid!</p>\n<p>To keep those lines so incredibly crisp, she used the tiniest brush she could find &#8211; <a href=\"https://www.sephora.com.au/search/full_search?utf8=✓&amp;q=Anastasia+Beverly+Hills+Brush&amp;button=\">Anastasia Beverly Hills Brush </a>to be exact &#8211; and worked slowly and steadily. You can choose any liners you like, but here&#8217;s an inside tip from Nicole herself. She didn&#8217;t use liner at all, but liquid lipsticks also from<a href=\"https://www.sephora.com.au/products/anastasia-beverly-hills-liquid-lipstick/v/milkshake-milk-with-a-hint-of-peach\"> Anastasia Beverly Hills</a>, but also <a href=\"https://jeffreestarcosmetics.com\">Jeffree Star</a> and <a href=\"http://www.ry.com.au/lime-crime-velvetines-metallic-liquid-matte-lipstick-2-6ml.html\">Lime Crime</a>.</p>\n<p>Her reason?</p>\n<p>&#8220;There&#8217;s a great colour range and they dry fast,&#8221; she told Allure.</p>\n<p>Well, of course.</p>\n<p>If you try this look please us know. We&#8217;d love to see how it turns out and also check out your awesome liner skills. And here&#8217;s some other looks created by Nicole just in case you fancy even more inspiration. Not enough? Go to<a href=\"https://www.instagram.com/p/BUbV9F_jJc8/?taken-by=nicoleetait&amp;hl=en\"> Nicole&#8217;s Instagram </a>account and you&#8217;ll find plenty more.</p>\n<figure id=\"attachment_2172\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2172\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000157/JPG-blue-liner-300x297.jpg\" alt=\"\" width=\"300\" height=\"297\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000157/JPG-blue-liner-300x297.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000157/JPG-blue-liner-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000157/JPG-blue-liner.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Blue, white and all-round Amazing. Image: Instagram/@nicoletait</figcaption></figure>\n<figure id=\"attachment_2173\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2173\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000328/JPG-Pink-red-liner-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000328/JPG-Pink-red-liner-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000328/JPG-Pink-red-liner-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000328/JPG-Pink-red-liner.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Pretty in pink. Image: Instagram/@nicoletait</figcaption></figure>\n<figure id=\"attachment_2174\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2174\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000424/JPG-Multi-colours-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000424/JPG-Multi-colours-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000424/JPG-Multi-colours-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/27000424/JPG-Multi-colours.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">All the colours of the rainbow. Image: Instagram/@nicoletait</figcaption></figure>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26231221/JPG-Graphic-.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"06640bf5-1bfc-5248-92be-84f6419493a0"}}

